import { Hono } from "hono";
import { decode, jwt, sign, verify } from "hono/jwt";
import { PrismaClient } from "@prisma/client/edge";
import { withAccelerate } from "@prisma/extension-accelerate";
import { signinInput, signupInput } from "@samiali/meduim-common";

type Environment = {
  Bindings: {
    DATABASE_URL: string;
    JWT_SECRET: string;
  };
};

export const userRouter = new Hono<Environment>();

userRouter.post("/signup", async (c) => {
  const UserBody = await c.req.json();
  const { success } = signupInput.safeParse(UserBody);
  if (!success) {
    c.status(411);
    return c.json({
      message: "Send Correct inputs ",
    });
  }
  const prisma = new PrismaClient({
    datasourceUrl: c.env.DATABASE_URL,
  }).$extends(withAccelerate());
  //@ts-ignore
  console.log(UserBody);

  try {
    const userData = await prisma.user.create({
      data: {
        email: UserBody.email,
        name: UserBody.name,
        password: UserBody.password,
      },
    });
    //@ts-ignore
    console.log(`db log - ${userData}`);

    const token = await sign({ id: userData.id }, c.env.JWT_SECRET);
    //@ts-ignore
    console.log(token);

    return c.json({
      messgae: "User Created successfully",
      token: token,
    });
  } catch (error) {
    c.status(411);
    return c.text("Invalid");
  }
});
userRouter.post("/signin", async (c) => {
  const body = await c.req.json();
  //@ts-ignore
  console.log(body);

  const result = signinInput.safeParse(body);
  if (!result.success) {
    c.status(400); // 400 = Bad Request
    return c.json({
      message: "Inputs not correct",
      errors: result.error.flatten(), // Optional: return validation errors
    });
  }
  //@ts-ignore
  console.log(result.data);

  const prisma = new PrismaClient({
    datasourceUrl: c.env.DATABASE_URL,
  }).$extends(withAccelerate());

  try {
    const FindUser = await prisma.user.findUnique({
      where: {
        email: body.email,
        password: body.password,
      },
    });
    if (!FindUser) {
      c.status(403);
      return c.json({
        message: "User not found",
      });
    }
    const token = await sign({ id: FindUser.id }, c.env.JWT_SECRET);

    return c.res.json({
      message: "successfully loged in",
      token: token,
    });
  } catch (error) {
    c.status(411);
    return c.text("Somthieng wrond");
  }
});
